.text-area {
  position: relative;
  padding-top: 28px;
  text-align: center;
}

.text-area__title {
  margin-top: 0;
  font-size: 36px;
  font-weight: 400;
  line-height: 36px;
}

.text-area__description {
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
}

.text-area::after {
  content: "";
  position: absolute;
  background-image: url("../img/index-can-mobile@1x.png");
  background-position: 50% 50%;
  background-repeat: no-repeat;
}

@media (max-width: 767px) {
  .text-area::after {
    top: 167px;
    left: 20px;
    width: 280px;
    height: 270px;
  }
}

@media (min-width: $tablet-width) {
  .text-area__title {
    font-size: 60px;
    line-height: 60px;
  }

  .text-area__description {
    font-size: 20px;
    line-height: 20px;
  }

  .text-area::after {
    z-index: -1;
    top: 230px;
    left: -89px;
    width: 709px;
    height: 609px;
    background-image: url("../img/index-can-tablet@1x.jpg");
    background-repeat: no-repeat;
    background-position: 50% 50%;
  }
}

@media (min-width: $desktop-width) {
  .text-area {
    position: static;
  }

  .text-area::after {
    z-index: 0;
    top: 142px;
    left: 38.9%;
    width: 552px;
    height: 499px;
    background-image: url("../img/index-can-desktop@1x.png");
    background-repeat: no-repeat;
    background-position: 50% 50%;
  }
}
