html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  min-width: 320px;
  font-family: "Oswald", "Arial", sans-serif;
}

img {
  max-width: 100%;
  height: auto;
}

@import "mixins.scss";
@import "variables.scss";
@import "normalize.scss";
@import "blocks/container.scss";
@import "blocks/header.scss";
@import "blocks/text-area.scss";
@import "blocks/button.scss";
@import "blocks/slider.scss";
@import "blocks/map.scss";
@import "blocks/footer.scss";
@import "blocks/social-buttons.scss";
@import "blocks/diets.scss";
@import "blocks/advantages.scss";
@import "blocks/example.scss";
@import "blocks/goods.scss";
