@import "blocks/extra-goods.scss";
@import "blocks/extra-card.scss";
@import "blocks/promo.scss";
@import "blocks/program.scss";

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}
